import React from 'react';
import { IMatrixMessage } from '../../../client/message/types';

interface RoomHistoryVisibilityMessageProps {
  message: IMatrixMessage;
  className?: string;
}

const visibilityMap: Record<string, string> = {
  shared: '',
  invited: '',
  joined: '',
  world_readable: '',
};

export const RoomHistoryVisibilityMessage = ({ message, className = '' }: RoomHistoryVisibilityMessageProps) => {
  const sender = message.sender;
  const visibility = message.content.history_visibility;
  const text = visibilityMap[visibility] || visibility;
  return (
    <div className={`room-history-visibility-message ${className}`} style={{ color: '#8e24aa', padding: '8px 0' }}>
      {sender ? `${sender} ${text}` : `${text}`}
    </div>
  );
}; 