import React from 'react';
import { Box, Button, Typography } from '@mui/material';
import { useProxyWatch } from '@fanfanlo';
import { roomData } from '@src/im/client/room/data';
import { useTranslation } from '@fanfanlo';
import { useRouter } from 'next/router';

export function MeetingPanel({ roomId }: { roomId: string }) {
  const [meetingActive] = useProxyWatch(roomData.meetingStateMap, [roomId, 'meetingActive'], false);
  const [meetingId] = useProxyWatch(roomData.meetingStateMap, [roomId, 'meetingId'], undefined);
  const { t } = useTranslation('homepage/im/components/meeting/content');
  const router = useRouter();
  if (!meetingActive) return null;
  function handleJoinMeeting() {
    if (!roomId || !meetingId) return;
    router.push({
      pathname: '/im/room/meeting',
      query: { roomId, callId: meetingId }
    });
  }
  return (
    <Box
      sx={{
        width: '100%',
        bgcolor: 'primary.light',
        color: 'primary.contrastText',
        p: 2,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        borderRadius: 1,
        mb: 2,
      }}
    >
      <Typography variant="body1">{t('MeetingPanel.meetingOngoing')}</Typography>
      <Button variant="contained" color="primary" onClick={handleJoinMeeting}>
        {t('MeetingPanel.joinMeeting')}
      </Button>
    </Box>
  );
} 