import { Direction } from 'matrix-js-sdk';
import { matrixClient } from '../../client';
import { messageUtils } from '../messageUtils';
import { IMatrixMessage } from '../../room/types';
//  matrix 
// import { matrixClient } from 'matrix';


/**
 *  event_id 
 * @param client MatrixClient 
 * @param roomId ID
 * @param eventId ID
 * @param limit 
 * @returns  MatrixEvent[]
 */
async function loadHistoryByEventId(roomId: string, eventId: string, limit: number = 20) {
    if(!matrixClient.client) return {
        events: [],
        isEnd: true,
        timeline: null,
    };
  // 1.  Room 
  const room = await matrixClient.client.getRoom(roomId);
  if (!room) {
    throw new Error('Room not found: ' + roomId);
  }

  // 2.  timelineSet
  const timelineSet = await room.getUnfilteredTimelineSet();
  if (!timelineSet) {
    throw new Error('timelineSet not found for room: ' + roomId);
  }

  // 3.  event  timeline
  const timeline = await matrixClient.client.getEventTimeline(timelineSet, eventId);
  if (!timeline) {
    throw new Error('timeline not found for event: ' + eventId);
  }

  // 4. 
  const hasMore = await matrixClient.client.paginateEventTimeline(timeline, {
    backwards: true,
    limit,
  });

  // 5.  timeline 
  const events = await timeline.getEvents(); // Array<MatrixEvent>
  
  // 6.  event  IMatrixMessage[]
  const messages = events
  .map(ev => ev.event)
  .filter(
    (ev): ev is {
      event_id: string;
      sender: string;
      content: any;
      origin_server_ts: number;
      type: string;
      unsigned?: any;
      room_id?: string;
    } =>
      !!ev &&
      typeof ev.event_id === 'string' &&
      typeof ev.sender === 'string' &&
      typeof ev.content === 'object' &&
      ev.content !== undefined &&
      typeof ev.origin_server_ts === 'number' &&
      typeof ev.type === 'string'
  )
  .map(ev => ({
    event_id: ev.event_id,
    sender: ev.sender,
    content: ev.content,
    origin_server_ts: ev.origin_server_ts,
    type: ev.type,
    unsigned: ev.unsigned,
    room_id: ev.room_id,
  })) as IMatrixMessage[];
  // 7. 
  messageUtils.batchAddHistoryMessages(roomId, messages);
  // 6. 
  const isEnd = !hasMore;

  return {
    events,
    isEnd,
    timeline,
  };
}

export const matrixLoadHistory = {
  loadHistoryByEventId
}