import React from 'react';
import { IMatrixMessage } from '../../client/message/types';
import { TextMessage } from './text/TextMessage';
import { LocationMessage } from './location/LocationMessage';
import { messageUtils } from '../../client/message/messageUtils';
import { InviteMessage } from './invite/InviteMessage';
import { RoomNameMessage } from './room/RoomNameMessage';
import { RoomHistoryVisibilityMessage } from './room/RoomHistoryVisibilityMessage';
import { RoomJoinRuleMessage } from './room/RoomJoinRuleMessage';
import { RoomPowerLevelsMessage } from './room/RoomPowerLevelsMessage';
import { RoomGuestAccessMessage } from './room/RoomGuestAccessMessage';
import { RoomTopicMessage } from './room/RoomTopicMessage';
import { RoomCreateMessage } from './room/RoomCreateMessage';
import { RoomAvatarMessage } from './room/RoomAvatarMessage';
import { RoomCanonicalAliasMessage } from './room/RoomCanonicalAliasMessage';
import { RoomTombstoneMessage } from './room/RoomTombstoneMessage';
import { RoomEncryptionMessage } from './room/RoomEncryptionMessage';
import { RoomServerAclMessage } from './room/RoomServerAclMessage';

interface MessageRendererProps {
  message: IMatrixMessage;
  className?: string;
}

export const MessageRenderer = ({ 
  message, 
  className = '' 
}: MessageRendererProps) => {
  const commonProps = {
    message,
    className
  };

  // 
  if (message.content && message.content.join_rule) {
    return <RoomJoinRuleMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // 
  if (message.content && (
    message.content.events_default !== undefined ||
    message.content.users !== undefined ||
    message.content.ban !== undefined ||
    message.content.kick !== undefined ||
    message.content.redact !== undefined
  )) {
    return <RoomPowerLevelsMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // 
  if (message.content && message.content.guest_access) {
    return <RoomGuestAccessMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // 
  if (message.content && message.content.topic) {
    return <RoomTopicMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // 
  if (message.content && message.content.history_visibility) {
    return <RoomHistoryVisibilityMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // 
  if (message.content && message.content.name) {
    return <RoomNameMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // 
  if (message.content && (message.content.membership === 'invite' || message.content.membership === 'join')) {
    return <InviteMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // 
  if (message.content && message.content.room_version && message.content.creator) {
    return <RoomCreateMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // 
  if (message.content && message.content.avatar_url) {
    return <RoomAvatarMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // 
  if (message.content && message.content.canonical_alias) {
    return <RoomCanonicalAliasMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // /
  if (message.content && message.content.replacement_room) {
    return <RoomTombstoneMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // 
  if (message.content && message.content.algorithm) {
    return <RoomEncryptionMessage key={message.event_id} {...commonProps} message={message} />;
  }

  // 
  if (message.content && (message.content.allow || message.content.deny)) {
    return <RoomServerAclMessage key={message.event_id} {...commonProps} message={message} />;
  }

  if (messageUtils.isTextMessage(message)) {
    return <TextMessage key={message.event_id} {...commonProps} message={message} />;
  }

  if (messageUtils.isLocationMessage(message)) {
    return <LocationMessage key={message.event_id} {...commonProps} message={message} />;
  }
  
  // ...
  console.log('unknown message=', message);
  console.log('json message=', JSON.stringify(message));
  return <div className={`unknown-message ${className}`}>
    : {message.content.msgtype}
  </div>;
};
