import { useRouter } from 'next/router';
import { useEffect } from 'react';
import { Dispatcher, useTranslation } from "@fanfanlo";
import { AppbarContainer, AppShell } from "@app/ui";
import { RoomContent } from '../../../im/components/room';
import { RoomContent2 } from '@src/im/components/room/RoomContent2';

export default function RoomPage() {
  const { t } = useTranslation("homepage/pages/im/room/content");
  const router = useRouter();
  const { id: roomId } = router.query;

  useEffect(() => {
    // roomId
    if (!roomId && router.isReady) {
      router.push('/im');
    }
  }, [roomId, router]);

  if (!roomId) {
    return (
      <AppShell>
        <AppbarContainer appbarProps={{ title: t("content.title"), back: true }} />
      </AppShell>
    );
  }

  // return(
  //   <AppShell>
  //     <RoomContentV2 roomId={roomId as string} dispatcher={new Dispatcher()} />
  //   </AppShell>
  // )
  return (
    
    <RoomContent2 roomId={roomId as string} />
  )

  return (
    <AppShell>
      
      {/* <AppbarContainer appbarProps={{ title: t("content.title"), back: true }}> */}
      <RoomContent roomId={roomId as string} />
      {/* </AppbarContainer> */}
    </AppShell>
  );
}