import React, { useState } from 'react';
import { IconButton, Menu, MenuItem } from '@mui/material';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { useRouter } from 'next/router';
import { useProxyWatch } from '@fanfanlo';
import { roomData } from '@src/im/client/room/data';
import { MeetingCreateDialog } from '../meeting/MeetingCreateDialog';
import { meetingEvent } from '@src/im/element-call/meetingEvent';

/**
 * 
 */
function CreateMeetingMenuItem() {
  const router = useRouter();
  // roomId 
  const roomId = typeof router.query.id === 'string' ? router.query.id : '';
  // useProxyWatch  [value, ...]
  const [disabled] = useProxyWatch(roomData.meetingStateMap, [roomId, 'meetingActive'], roomData.meetingStateMap[roomId]?.meetingActive || false);
  const [dialogOpen, setDialogOpen] = useState(false);

  function handleMenuClick() {
    if (!disabled) setDialogOpen(true);
  }
  function handleDialogClose() {
    setDialogOpen(false);
  }
  async function handleDialogSubmit(data: { name: string }) {
    setDialogOpen(false);
    if (!roomId) return;
    // 
    await meetingEvent.sendStartMeetingEvent(roomId, { type: 'normal' });
    // toast.success('')
  }

  return (
    <>
      <MenuItem
        onClick={handleMenuClick}
        disabled={disabled}
      >
        
      </MenuItem>
      <MeetingCreateDialog
        open={dialogOpen}
        onClose={handleDialogClose}
        onCreate={handleDialogSubmit}
      />
    </>
  );
}

/**
 * 
 */
export function HeaderExtraButton() {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  return (
    <>
      <IconButton
        color="inherit"
        onClick={e => setAnchorEl(e.currentTarget)}
        size="large"
      >
        <MoreVertIcon />
      </IconButton>
      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={() => setAnchorEl(null)}
      >
        <CreateMeetingMenuItem />
        {/*  */}
      </Menu>
    </>
  );
} 