import { Box, Paper, Divider, useTheme, CssBaseline } from '@mui/material';
import { useTranslation } from '@fanfanlo';
import { RoomHeader } from './RoomHeader';
import { MessageList } from './MessageList';
import { MessageInput } from './MessageInput';
import { RoomMembers } from './RoomMembers';
import { useCallback, useEffect, useRef, useState } from 'react';
import { useRouter } from 'next/router';
import { MessageListScroller, MessageListScrollerRef } from './MessageListScroller';

interface RoomContentProps {
  roomId: string;
}

export function RoomContent({ roomId }: RoomContentProps) {
  const { t } = useTranslation('homepage/im/components/room/content');
  const theme = useTheme();
  const [showMembers, setShowMembers] = useState(false);

  const router = useRouter();
  const toggleMembers = () => {
    setShowMembers(!showMembers);
  };
  const topBarRef = useRef<HTMLDivElement>(null);
  const bottomBarRef = useRef<HTMLDivElement>(null);
  const [bottomBarHeight, setBottomBarHeight] = useState(0);
  const [topBarHeight, setTopBarHeight] = useState(0);
  const [hasMoreMessages, setHasMoreMessages] = useState(true);
  const [hasNewerMessages, setHasNewerMessages] = useState(true);
  const [isLoadingMore, setIsLoadingMore] = useState(false);
  const [isLoadingNewer, setIsLoadingNewer] = useState(false);
  const messageScrollerRef = useRef<MessageListScrollerRef>(null);

  useEffect(() => {
    if (bottomBarRef.current) {
      setBottomBarHeight(bottomBarRef.current.offsetHeight);
    }
  }, [bottomBarRef.current]);

  useEffect(() => {
    if (topBarRef.current) {
      setTopBarHeight(topBarRef.current.offsetHeight);
    }
  }, [topBarRef.current]);
  // 
  const handleLoadMore = useCallback(async () => {
    if (isLoadingMore || !hasMoreMessages) return;
    
    setIsLoadingMore(true);
    try {
      // TODO: 
      // : await loadOlderMessages(roomId);
      //  hasMoreMessages 
      // setHasMoreMessages(/*  */);
      await new Promise(resolve => setTimeout(resolve, 1000)); // 
    } finally {
      setIsLoadingMore(false);
    }
  }, [roomId, isLoadingMore, hasMoreMessages]);

  // 
  const handleLoadNewer = useCallback(async () => {
    if (isLoadingNewer || !hasNewerMessages) return;
    
    setIsLoadingNewer(true);
    try {
      // TODO: 
      // : await loadNewerMessages(roomId);
      //  hasNewerMessages 
      // setHasNewerMessages(/*  */);
      await new Promise(resolve => setTimeout(resolve, 1000)); // 
    } finally {
      setIsLoadingNewer(false);
    }
  }, [roomId, isLoadingNewer, hasNewerMessages]);

  // 
  const scrollToBottom = useCallback(() => {
    if (messageScrollerRef.current) {
      messageScrollerRef.current.scrollToBottom();
    }
  }, []);

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
      {/*  AppBar */}
      <Box
        ref={topBarRef}
        sx={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          backgroundColor: '#fff',
          padding: '10px',
          borderBottom: '1px solid #ccc',
          zIndex: 1,
        }}>
        <RoomHeader roomId={roomId} onToggleMembers={toggleMembers} showMembers={showMembers} />
      </Box>

      {/*  */}
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          height: `calc(100vh - ${topBarHeight + bottomBarHeight}px)`,
          marginTop: `${topBarHeight}px`,
          overflow: 'hidden',
          backgroundColor: '#e0e0e0',
        }}>
        <Box
          sx={{
            flex: 1,
            padding: 2,
            height: '100%',
            boxSizing: 'border-box',
            position: 'relative',
          }}>
          <MessageListScroller 
            ref={messageScrollerRef}
            roomId={roomId}
            onLoadMore={handleLoadMore}
            onLoadNewer={handleLoadNewer}
            hasMore={hasMoreMessages}
            hasNewer={hasNewerMessages}
            loading={isLoadingMore}
            loadingNewer={isLoadingNewer}
          >
            <MessageList roomId={roomId} onMessageSent={scrollToBottom} />
          </MessageListScroller>
        </Box>
      </Box>

      {/*  BottomBar */}
      <Box
        ref={bottomBarRef}
        sx={{
          position: 'fixed',
          bottom: 0,
          left: 0,
          right: 0,
          backgroundColor: '#fff',
          padding: '10px',
          borderTop: '1px solid #ccc',
          zIndex: 1,
        }}>
        <MessageInput roomId={roomId} />
      </Box>
    </Box>
  );
}
