import { Log, useTranslation } from '@fanfanlo';
import { Box, useTheme } from '@mui/material';
import { useRouter } from 'next/router';
import { useCallback, useEffect, useRef, useState } from 'react';
import { MessageInput } from './MessageInput';
import { MessageListVirtuoso } from './MessageListVirtuoso';
import { RoomHeader } from './RoomHeader';
import { MeetingPanel } from '../meeting/MeetingPanel';
// import {MessageVirturalList} from './MessageVirturalList'

const fileLogger = new Log(false, 'im.components.room.RoomContent2');
// fileLogger.pause = true;

interface RoomContent2Props {
  roomId: string;
}

export function RoomContent2({ roomId }: RoomContent2Props) {
  const logger = fileLogger.sub(false, 'RoomContent2');
  // logger.pause = true;
  
  const { t } = useTranslation('homepage/im/components/room/content');
  const theme = useTheme();
  const [showMembers, setShowMembers] = useState(false);
  const router = useRouter();
  
  const topBarRef = useRef<HTMLDivElement>(null);
  const bottomBarRef = useRef<HTMLDivElement>(null);
  const [bottomBarHeight, setBottomBarHeight] = useState(0);
  const [topBarHeight, setTopBarHeight] = useState(0);

  useEffect(() => {
    if (bottomBarRef.current) {
      setBottomBarHeight(bottomBarRef.current.offsetHeight);
    }
  }, [bottomBarRef.current]);

  useEffect(() => {
    if (topBarRef.current) {
      setTopBarHeight(topBarRef.current.offsetHeight);
    }
  }, [topBarRef.current]);

  // 
  const scrollToBottom = useCallback(() => {
    //  MessageListScroller
    // 
  }, []);

  // 
  const toggleMembers = useCallback(() => {
    setShowMembers(prev => !prev);
  }, []);

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
      {/*  AppBar */}
      <Box
        ref={topBarRef}
        sx={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          backgroundColor: '#fff',
          padding: '10px',
          borderBottom: '1px solid #ccc',
          zIndex: 1,
        }}>
        <RoomHeader roomId={roomId} onToggleMembers={toggleMembers} showMembers={showMembers} />
      </Box>

      {/*  */}
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          height: `calc(100vh - ${topBarHeight + bottomBarHeight}px)`,
          marginTop: `${topBarHeight}px`,
          overflow: 'hidden',
          backgroundColor: '#e0e0e0',
        }}>
        <Box
          sx={{
            flex: 1,
            padding: 2,
            height: '100%',
            boxSizing: 'border-box',
            position: 'relative',
            overflowY: 'hidden',
          }}>
        <MeetingPanel roomId={roomId} />
            <MessageListVirtuoso roomId={roomId} />
            {/* <MessageListVirtuosoTest /> */}
          {/* <MessageList roomId={roomId} /> */}
          {/* <MessageVirturalList roomId={roomId} /> */}
          {/* <VirtualListScrollToBottom /> */}
        </Box>
      </Box>

      {/*  BottomBar */}
      <Box
        ref={bottomBarRef}
        sx={{
          position: 'fixed',
          bottom: 0,
          left: 0,
          right: 0,
          backgroundColor: '#fff',
          padding: '10px',
          borderTop: '1px solid #ccc',
          zIndex: 1,
        }}>
        <MessageInput roomId={roomId} />
      </Box>
    </Box>
  );
}