import { Log, useProxyWatch, useProxyWatchUpdates, useTranslation } from '@fanfanlo';
import { Box, CircularProgress, Typography } from '@mui/material';
import { useEffect, useRef, useState, useLayoutEffect } from 'react';
import { IMatrixMessage, matrixRoom } from '../../client/room';
import { matrixUser } from '../../client/user';
import { MessageItem } from './MessageItem';
const fileLogger = new Log(false, 'im.components.room.MessageList')
// fileLogger.childrenPaused = true;
// fileLogger.pause = true;
interface MessageListProps {
  roomId: string;
}

export function MessageList({ roomId }: MessageListProps) {
  const logger = fileLogger.sub(false, 'MessageList_ui');
  logger.pause = true;
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const messagesContainerRef = useRef<HTMLDivElement>(null);
  const [autoScroll, setAutoScroll] = useState(true);
  const wasAtBottomRef = useRef(true);
  const userAtBottomRef = useRef(true);
  const userReallyAtBottomRef = useRef(true);
  const { t } = useTranslation('homepage/im/components/room/content');
  const DISTANCE_TO_BOTTOM = 5; // px
  const prevMessageCountRef = useRef(0);

  // 
  function isNearBottom(distance = DISTANCE_TO_BOTTOM) {
    if (!messagesContainerRef.current) return false;
    const { scrollTop, scrollHeight, clientHeight } = messagesContainerRef.current;
    return scrollHeight - scrollTop - clientHeight <= distance;
  }
  
  // 
  const [loginResponse] = useProxyWatch(
    matrixUser.userData,
    'userInfo.loginResponse',
    matrixUser.userData.userInfo.loginResponse
  );
  
  // 
  const [messageIds] = useProxyWatchUpdates(
    matrixRoom.roomData.messages,
    [`${roomId}`],
    matrixRoom.roomData.messages[roomId] || []
  );
  const messagesMap = matrixRoom.roomData.messagesMap;
  //  IMatrixMessage 
  const messageList = messageIds?.map(id => {
    const message = messagesMap[id];
    if (!message) {
      console.warn('Message not found:', id);
      return null;
    }
    
    //  IMatrixMessage 
    const res:IMatrixMessage = {
      event_id: message.event_id,
      type: message.content.msgtype,
      sender: message.sender,
      origin_server_ts: message.origin_server_ts,
      room_id: roomId,
      content: message.content,
    };
    return res;
  }).filter((item)=>{return item !== null}) || [];

  // 
  const scrollToBottom = () => {
    console.log('[MessageList] scrollToBottom exec', {
      messagesEndRef: messagesEndRef.current,
      messagesContainerRef: messagesContainerRef.current,
      scrollTop: messagesContainerRef.current?.scrollTop,
      scrollHeight: messagesContainerRef.current?.scrollHeight,
    });
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  };

  // ""
  const handleScroll = () => {
    const atBottom = isNearBottom();
    userReallyAtBottomRef.current = atBottom;
    console.log('[MessageList] handleScroll, atBottom:', atBottom, 'autoScroll:', autoScroll);
    setAutoScroll(atBottom);
  };

  // ""
  useLayoutEffect(() => {
    wasAtBottomRef.current = userReallyAtBottomRef.current;
    console.log('[MessageList] useLayoutEffect([messageList]), wasAtBottomRef.current:', wasAtBottomRef.current);
  }, [messageList]);

  // 
  useEffect(() => {
    console.log('[MessageList] useEffect([messageList, autoScroll]), wasAtBottomRef.current:', wasAtBottomRef.current, 'autoScroll:', autoScroll, 'messageList.length:', messageList.length);
    if (wasAtBottomRef.current && autoScroll) {
      setTimeout(() => {
        scrollToBottom();
      }, 0);
    }
  }, [messageList, autoScroll]);

  // 
  useEffect(() => {
    if (prevMessageCountRef.current === 0 && messageList.length > 0) {
      setTimeout(() => {
        scrollToBottom();
      }, 0);
    }
    prevMessageCountRef.current = messageList.length;
  }, [messageList]);

  // autoScroll  true
  useEffect(() => {
    setAutoScroll(true);
  }, [roomId]);

  return (
    <Box id="messagesContainer"
      ref={messagesContainerRef}
      onScroll={handleScroll}
      sx={{
        height: '100%',
        flex: 1,
        overflowY: 'auto',
        p: 2,
        display: 'flex',
        flexDirection: 'column',
        gap: 2
      }}
    >
      {!loginResponse?.user_id ? (
        <Box display="flex" justifyContent="center" alignItems="center" height="100%">
          <CircularProgress />
        </Box>
      ) : messageList.length === 0 ? (
        <Box display="flex" justifyContent="center" alignItems="center" height="100%">
          <Typography variant="body1" color="text.secondary">
            {t('MessageList.noMessages')}
          </Typography>
        </Box>
      ) : (
        messageList.map((message) => (
          <MessageItem 
            key={message.event_id}
            message={message}
            currentUserId={loginResponse.user_id}
          />
        ))
      )}
      <div ref={messagesEndRef} />
    </Box>
  );
}
