import { matrixClient } from '../client/client';
import type { IMeetingState } from './meetingState.types';
import { MATRIX_EVENT_TYPE_MEETING } from './meetingEvent.types';

/**
 *  callId
 */
function generateCallId() {
  if (typeof crypto !== 'undefined' && crypto.randomUUID) {
    return crypto.randomUUID();
  }
  // fallback
  return Math.random().toString(36).slice(2) + Date.now();
}

/**
 * org.matrix.msc3401.call
 * @param roomId ID
 * @param options 
 */
export async function sendStartMeetingEvent(roomId: string, options?: Partial<Pick<IMeetingState, 'type'>>) {
  const callId = generateCallId();
  const userId = matrixClient.client.getUserId?.() || '';
  const now = Date.now();
  const content = {
    state: 'started',
    call_id: callId,
    initiator: userId,
    type: options?.type || 'normal',
    started_at: now,
  };
  // @ts-ignore: 
  await matrixClient.client.sendEvent(roomId, MATRIX_EVENT_TYPE_MEETING as any, content, '');
  return callId;
}

/**
 * 
 */
export async function sendEndMeetingEvent(roomId: string, callId: string) {
  const userId = matrixClient.client.getUserId?.() || '';
  const now = Date.now();
  const content = {
    state: 'ended',
    call_id: callId,
    initiator: userId,
    ended_at: now,
  };
  // @ts-ignore: 
  await matrixClient.client.sendEvent(roomId, MATRIX_EVENT_TYPE_MEETING as any, content, '');
}

export const meetingEvent = {
  sendStartMeetingEvent,
  sendEndMeetingEvent,
}; 