import React from 'react';
import { IMatrixMessage } from '../../../client/message/types';

interface RoomJoinRuleMessageProps {
  message: IMatrixMessage;
  className?: string;
}

const joinRuleMap: Record<string, string> = {
  invite: '',
  public: '',
  knock: '',
  restricted: '',
};

export const RoomJoinRuleMessage = ({ message, className = '' }: RoomJoinRuleMessageProps) => {
  const sender = message.sender;
  const joinRule = message.content.join_rule;
  const text = joinRuleMap[joinRule] || joinRule;
  return (
    <div
      className={`room-join-rule-message ${className}`}
      style={{
        color: '#fff',
        background: '#0288d1',
        borderRadius: 6,
        padding: '8px 12px',
        fontWeight: 500,
        display: 'inline-block',
        maxWidth: '100%',
      }}
    >
      {sender ? `${sender} ${text}` : `${text}`}
    </div>
  );
}; 