import React, { useEffect, useRef, useState, useCallback } from 'react';
import { ListRange, Virtuoso } from 'react-virtuoso';
import { isDev, useTranslation } from '@fanfanlo';
import { MessageItem } from './MessageItem';
import { matrixRoom } from '../../client/room';
import { matrixUser } from '../../client/user';
import { useProxyWatch, useProxyWatchUpdates } from '@fanfanlo';
import { IMatrixMessage } from '@src/im/client';
import { matrixLoadHistory } from '@src/im/client/message/handlers/loadHistory';
import { Log } from '@fanfanlo';

interface MessageListVirtuosoProps {
  roomId: string;
}

const fileLogger = new Log(false, 'MessageListVirtuoso');
// fileLogger.pause = true;
// fileLogger.childrenPaused = true;

export function MessageListVirtuoso({ roomId }: MessageListVirtuosoProps) {
  const logger = fileLogger.sub(false,'MessageListVirtuoso_ui');
  logger.pause = true;
  // logger.childrenPaused = true;

  const { t } = useTranslation('homepage/im/components/room/content');

  // 
  const [loginResponse] = useProxyWatch(
    matrixUser.userData,
    'userInfo.loginResponse',
    matrixUser.userData.userInfo.loginResponse
  );

  // 
  const [messageIds] = useProxyWatchUpdates(
    matrixRoom.roomData.messages,
    [`${roomId}`],
    matrixRoom.roomData.messages[roomId] || []
  );
  const messagesMap = matrixRoom.roomData.messagesMap;

  //  IMatrixMessage 
  const messageList = (messageIds?.map(id => {
    const message = messagesMap[id];
    if (!message) return null;
    return {
      event_id: message.event_id,
      type: message.content.msgtype,
      sender: message.sender,
      origin_server_ts: message.origin_server_ts,
      room_id: roomId || '',
      content: message.content,
    };
  }).filter((item): item is NonNullable<typeof item> => item !== null) || []) as IMatrixMessage[];

  const [loading, setLoading] = useState(false);
  const [historyEnd, setHistoryEnd] = useState(false);
  const [loadingHistory, setLoadingHistory] = useState(false);
  const virtuosoRef = useRef<any>(null);
  const containerRef = useRef<HTMLDivElement>(null);
  // 
  const anchorMsgIdRef = useRef<string | null>(null); // id
  const [autoCompensate, setAutoCompensate] = useState(false);

  //  anchorMsgIdRef
  const onLoadMore = useCallback(async () => {
    logger.log('onLoadMore called', {
      roomId,
      messageListLen: messageList.length,
      loading,
      historyEnd,
    });
    if (!roomId || messageList.length === 0 || loading || historyEnd) return;
    setLoading(true);
    setLoadingHistory(true);
    try {
      anchorMsgIdRef.current = messageList[0]?.event_id;
      const res = await matrixLoadHistory.loadHistoryByEventId(roomId, messageList[0].event_id, isDev ? 30 : 10);
      logger.log('res=', res);
      if (res.isEnd) setHistoryEnd(true);
      setAutoCompensate(true); // 
    } finally {
      setLoading(false);
      setLoadingHistory(false);
    }
  }, [roomId, messageList, loading, historyEnd]);

  //  totalListHeightChanged 
  const handleTotalListHeightChanged = useCallback((height: number) => {
    logger.log('[totalListHeightChanged] compensation check', { height, autoCompensate, anchorMsg: anchorMsgIdRef.current });
    if (autoCompensate && anchorMsgIdRef.current) {
      const newIndex = messageList.findIndex(msg => msg.event_id === anchorMsgIdRef.current);
      logger.log('[autoCompensate] anchorMsgId', anchorMsgIdRef.current, 'newIndex', newIndex);
      if (newIndex !== -1) {
        setTimeout(() => {
          logger.log('[autoScroll] scrollToIndex', { newIndex, anchorMsgId: anchorMsgIdRef.current });
          virtuosoRef.current?.scrollToIndex({ index: newIndex, align: 'start', 
            // behavior: 'smooth' 
          });
        }, 30);
      }
      setAutoCompensate(false);
    }
  }, [autoCompensate, messageList]);

  // 
  useEffect(() => {
    const stopCompensate = () => {
      logger.log('[userAction] stop autoCompensate');
      setAutoCompensate(false);
    };
    const el = containerRef.current;
    if (!el) return;
    el.addEventListener('mousedown', stopCompensate);
    el.addEventListener('touchstart', stopCompensate);
    return () => {
      el.removeEventListener('mousedown', stopCompensate);
      el.removeEventListener('touchstart', stopCompensate);
    };
  }, []);

  // 
  const handleAtTopStateChange = useCallback((atTop: boolean) => {
    logger.log('[atTopStateChange]', { atTop, historyEnd, loading, loadingHistory });
    if (atTop && !historyEnd && !loading && !loadingHistory) {
      onLoadMore();
    }
  }, [historyEnd, loading, loadingHistory, onLoadMore]);

  logger.log('messageList.length=', messageList.length);
  return (
    // <div ref={containerRef} style={{ height: '100%', width: '100%' }}>
      <Virtuoso
        ref={virtuosoRef}
        totalListHeightChanged={handleTotalListHeightChanged}
        style={{ height: '100%', width: '100%' }}
        data={messageList}
        itemContent={(index, message) => (
          <MessageItem
            message={message}
            currentUserId={loginResponse?.user_id || ''}
          />
        )}
        followOutput={"auto"}
        atTopStateChange={handleAtTopStateChange}
        components={{
          EmptyPlaceholder: () => (
            <div style={{ textAlign: 'center', color: '#888', padding: 16 }}>
              {t('MessageList.noMessages')}
            </div>
          ),
          Header: () =>
            loading ? (
              <div style={{ textAlign: 'center', color: '#888', padding: 8 }}>
                {t('MessageList.loadingHistory')}
              </div>
            ) : historyEnd ? (
              <div style={{ textAlign: 'center', color: '#888', padding: 8 }}>
                {t('MessageList.historyEnd')}
              </div>
            ) : null,
        }}
      />
    // </div>
  );
}

