import React, {
  forwardRef,
  useRef,
  useState,
  useCallback,
  useEffect,
  useImperativeHandle,
  type ReactNode,
  type CSSProperties,
} from 'react';
import { Box, Fade, CircularProgress, Button, Badge } from '@mui/material';
import { useTranslation } from 'react-i18next';

interface ScrollInfo {
  scrollTop: number;
  scrollHeight: number;
  clientHeight: number;
  distanceFromBottom: number;
  isAtTop: boolean;
  isAtBottom: boolean;
  timestamp: number;
}

export interface MessageListScrollerRef {
  scrollToBottom: (behavior?: ScrollBehavior) => void;
  scrollToTop: (behavior?: ScrollBehavior) => void;
  scrollToPosition: (position: number) => void;
  getScrollPosition: () => number;
  loadOlderMessages: () => Promise<void>;
  loadNewerMessages: () => Promise<void>;
}

export interface MessageListScrollerProps {
  children: ReactNode;
  onLoadMore?: () => Promise<void>;
  onLoadNewer?: () => Promise<void>;
  hasMore: boolean;
  hasNewer: boolean;
  loading: boolean;
  loadingNewer: boolean;
  className?: string;
  style?: CSSProperties;
  roomId: string;
}

const SCROLL_THRESHOLD = 100;
const SCROLL_DEBOUNCE_MS = 150;

type ScrollDirection = 'up' | 'down' | null;

const MessageListScroller = forwardRef<MessageListScrollerRef, MessageListScrollerProps>((
  {
    children,
    onLoadMore = async () => {},
    onLoadNewer = async () => {},
    hasMore = false,
    hasNewer = false,
    loading = false,
    loadingNewer = false,
    className = '',
    style = {},
    roomId,
  },
  ref
) => {
  const { t } = useTranslation('homepage/im/components/room/content');
  const listRef = useRef<HTMLDivElement>(null);
  const debounceTimerRef = useRef<NodeJS.Timeout>();
  const scrollTimeoutRef = useRef<NodeJS.Timeout>();
  const isAtBottomRef = useRef(true);
  const scrollDirectionRef = useRef<ScrollDirection>(null);
  // 
  const lastScrollTopRef = useRef<{ scrollTop: number; time?: number }>({ scrollTop: 0 });
  const isLoadingOlderRef = useRef(false);
  const isLoadingNewerRef = useRef(false);
  const isInitialLoadRef = useRef(true);
  const isProgrammaticScrollRef = useRef(false);
  const scrollAnimationFrameRef = useRef<number>();

  const [showScrollToBottom, setShowScrollToBottom] = useState(false);
  const [newMessageCount, setNewMessageCount] = useState(0);

  // 
  const updateScrollInfo = useCallback((el: HTMLDivElement): ScrollInfo | null => {
    if (!el) return null;
    
    const { scrollTop, scrollHeight, clientHeight } = el;
    const distanceFromBottom = scrollHeight - scrollTop - clientHeight;
    // 
    const isAtTop = scrollTop <= SCROLL_THRESHOLD * 1.5;
    const isAtBottom = distanceFromBottom <= SCROLL_THRESHOLD;
    
    // 
    const SCROLL_THRESHOLD_PX = 1; // 1px threshold to reduce noise
    const scrollDiff = scrollTop - lastScrollTopRef.current.scrollTop;
    
    if (Math.abs(scrollDiff) > SCROLL_THRESHOLD_PX) {
      scrollDirectionRef.current = scrollDiff > 0 ? 'down' : 'up';
      lastScrollTopRef.current = { ...lastScrollTopRef.current, scrollTop };
      
      console.log('Scroll direction updated:', {
        direction: scrollDirectionRef.current,
        scrollTop,
        lastScrollTop: lastScrollTopRef.current.scrollTop,
        diff: scrollDiff
      });
    }
    
    // 
    isAtBottomRef.current = isAtBottom;
    
    return {
      scrollTop,
      scrollHeight,
      clientHeight,
      distanceFromBottom,
      isAtTop,
      isAtBottom,
      timestamp: Date.now(),
    };
  }, []);

  // 
  const scrollToBottom = useCallback((behavior: ScrollBehavior = 'smooth') => {
    const list = listRef.current;
    if (!list) return;
    
    // 
    const { scrollTop, scrollHeight, clientHeight } = list;
    const isAtBottom = scrollHeight - (scrollTop + clientHeight) < 10;
    if (isAtBottom) {
      isAtBottomRef.current = true;
      setShowScrollToBottom(false);
      setNewMessageCount(0);
      return;
    }
    
    // 
    isProgrammaticScrollRef.current = true;
    
    // scrollTopscrollToscroll
    list.scrollTop = list.scrollHeight;
    
    // UI
    isAtBottomRef.current = true;
    setShowScrollToBottom(false);
    setNewMessageCount(0);
    
    // requestAnimationFrameDOM
    requestAnimationFrame(() => {
      // 
      if (list) {
        const { scrollTop, scrollHeight, clientHeight } = list;
        isAtBottomRef.current = scrollHeight - (scrollTop + clientHeight) < 10;
      }
      // 
      setTimeout(() => {
        isProgrammaticScrollRef.current = false;
      }, 100);
    });
  }, []);

  // 
  const scrollToTop = useCallback((behavior: ScrollBehavior = 'smooth') => {
    if (listRef.current) {
      listRef.current.scrollTo({
        top: 0,
        behavior,
      });
    }
  }, []);

  // 
  const scrollToPosition = useCallback((position: number) => {
    if (listRef.current) {
      listRef.current.scrollTop = position;
    }
  }, []);

  // 
  const getScrollPosition = useCallback((): number => {
    return listRef.current?.scrollTop || 0;
  }, []);

  // 
  const loadOlderMessages = useCallback(async () => {
    console.log('loadOlderMessages called');
    if (!hasMore || loading || isLoadingOlderRef.current) {
      console.log('Skipping loadOlderMessages:', { hasMore, loading, isLoadingOlder: isLoadingOlderRef.current });
      return;
    }
    
    console.log('Loading older messages...');
    isLoadingOlderRef.current = true;
    const list = listRef.current;
    if (!list) {
      console.log('No list element');
      isLoadingOlderRef.current = false;
      return;
    }

    // 
    const scrollTop = list.scrollTop;
    const scrollHeight = list.scrollHeight;
    const isAtTop = scrollTop <= SCROLL_THRESHOLD;
    const firstChild = list.firstElementChild as HTMLElement;
    const firstChildOffset = firstChild ? firstChild.offsetTop : 0;
    
    console.log('Before load:', { scrollTop, scrollHeight, isAtTop, firstChildOffset });

    try {
      // 
      isProgrammaticScrollRef.current = true;
      
      // 
      await onLoadMore();
      
      //  requestAnimationFrame  DOM 
      requestAnimationFrame(() => {
        if (!list) return;
        
        const newScrollHeight = list.scrollHeight;
        const heightDiff = newScrollHeight - scrollHeight;
        console.log('After load:', { newScrollHeight, heightDiff });
        
        // 
        if (isAtTop && heightDiff > 0) {
          console.log('Restoring scroll position:', { newPosition: heightDiff + scrollTop });
          
          // 1
          list.scrollTop = heightDiff + scrollTop;
          
          // 2
          if (firstChild) {
            const newFirstChild = list.firstElementChild as HTMLElement;
            if (newFirstChild) {
              setTimeout(() => {
                const newPosition = newFirstChild.offsetTop - firstChildOffset + scrollTop;
                console.log('Alternative scroll position:', newPosition);
                list.scrollTop = newPosition;
              }, 0);
            }
          }
        }
        
        // 
        setTimeout(() => {
          isProgrammaticScrollRef.current = false;
        }, 100);
      });
    } catch (error) {
      console.error('Failed to load older messages:', error);
      isProgrammaticScrollRef.current = false;
    } finally {
      console.log('Finished loading older messages');
      isLoadingOlderRef.current = false;
    }
  }, [hasMore, loading, onLoadMore]);

  // 
  const loadNewerMessages = useCallback(async () => {
    console.log('loadNewerMessages called');
    if (!hasNewer || loadingNewer || isLoadingNewerRef.current) return;
    
    isLoadingNewerRef.current = true;
    const list = listRef.current;
    if (!list) {
      isLoadingNewerRef.current = false;
      return;
    }

    const wasAtBottom = isAtBottomRef.current;

    try {
      await onLoadNewer();

      // 
      setTimeout(() => {
        if (!list) return;
        
        const { scrollTop, scrollHeight, clientHeight } = list;
        const isStillAtBottom = scrollHeight - (scrollTop + clientHeight) <= SCROLL_THRESHOLD;

        if (wasAtBottom && isStillAtBottom) {
          scrollToBottom('smooth');
        } else if (!wasAtBottom) {
          setNewMessageCount(prev => prev + 1);
        }
      }, 100);
    } catch (error) {
      console.error('Failed to load newer messages:', error);
    } finally {
      isLoadingNewerRef.current = false;
    }
  }, [hasNewer, loadingNewer, onLoadNewer, scrollToBottom]);

  // 
  const handleScroll = useCallback((e: React.UIEvent<HTMLDivElement>) => {
    const el = e.currentTarget;
    if (!el) return;

    // 
    if (isProgrammaticScrollRef.current) {
      console.log('Programmatic scroll, skipping scroll handling');
      return;
    }

    // 
    if (debounceTimerRef.current) {
      clearTimeout(debounceTimerRef.current);
    }

    // 
    const currentScrollTop = el.scrollTop;
    const currentTime = Date.now();
    
    // 
    const scrollDiff = currentScrollTop - lastScrollTopRef.current.scrollTop;
    if (Math.abs(scrollDiff) > 1) { // 1px threshold
      scrollDirectionRef.current = scrollDiff > 0 ? 'down' : 'up';
    }
    
    // 
    lastScrollTopRef.current = {
      scrollTop: currentScrollTop,
      time: currentTime
    };
    
    console.log('Scroll event:', {
      scrollTop: currentScrollTop,
      direction: scrollDirectionRef.current,
      isProgrammatic: isProgrammaticScrollRef.current
    });

    // 
    debounceTimerRef.current = setTimeout(() => {
      const scrollInfo = updateScrollInfo(el);
      if (!scrollInfo) return;

      const { isAtTop, isAtBottom, scrollTop, scrollHeight, clientHeight } = scrollInfo;
      
      console.log('Scroll info:', { 
        isAtTop, 
        isAtBottom, 
        scrollTop, 
        scrollHeight, 
        clientHeight, 
        direction: scrollDirectionRef.current,
        isProgrammatic: isProgrammaticScrollRef.current
      });
      
      // UI
      setShowScrollToBottom(!isAtBottom);
      isAtBottomRef.current = isAtBottom;

      // 
      console.log('Scroll state:', {
        isAtTop,
        isAtBottom,
        hasMore,
        hasNewer,
        loading,
        loadingNewer,
        scrollDirection: scrollDirectionRef.current,
        isProgrammatic: isProgrammaticScrollRef.current
      });
      
      // 
      if (!isProgrammaticScrollRef.current) {
        // 
        if (isAtTop && hasMore && !loading && scrollDirectionRef.current === 'up') {
          console.log('Loading older messages...');
          loadOlderMessages().catch(error => {
            console.error('Error loading older messages:', error);
            // 
            isLoadingOlderRef.current = false;
          });
        }
        // 
        else if (isAtBottom && hasNewer && !loadingNewer && scrollDirectionRef.current === 'down') {
          console.log('Loading newer messages...');
          loadNewerMessages().catch(error => {
            console.error('Error loading newer messages:', error);
            // 
            isLoadingNewerRef.current = false;
          });
        }
      }
    }, SCROLL_DEBOUNCE_MS);
  }, [hasMore, hasNewer, loading, loadingNewer, loadOlderMessages, loadNewerMessages, updateScrollInfo]);

  // 
  useEffect(() => {
    if (isInitialLoadRef.current && listRef.current) {
      isInitialLoadRef.current = false;
      
      console.log('Initial load, scrolling to bottom...');
      
      //  setTimeout 
      const timer = setTimeout(() => {
        if (!listRef.current) return;
        
        const { scrollHeight, clientHeight, scrollTop } = listRef.current;
        const isAtBottom = scrollHeight - (scrollTop + clientHeight) < 10;
        
        console.log('Initial scroll check:', { scrollHeight, clientHeight, scrollTop, isAtBottom });
        
        // 
        if (!isAtBottom) {
          console.log('Scrolling to bottom...');
          scrollToBottom('auto');
        } else {
          console.log('Already at bottom, no need to scroll');
        }
      }, 100);
      
      return () => {
        clearTimeout(timer);
      };
    }
  }, [scrollToBottom]);

  // 
  useEffect(() => {
    return () => {
      if (debounceTimerRef.current) clearTimeout(debounceTimerRef.current);
      if (scrollTimeoutRef.current) clearTimeout(scrollTimeoutRef.current);
      if (scrollAnimationFrameRef.current) {
        cancelAnimationFrame(scrollAnimationFrameRef.current);
      }
    };
  }, []);

  // 
  useImperativeHandle(
    ref,
    () => ({
      scrollToBottom,
      scrollToTop,
      scrollToPosition,
      getScrollPosition,
      loadOlderMessages,
      loadNewerMessages,
    }),
    [scrollToBottom, scrollToTop, scrollToPosition, getScrollPosition, loadOlderMessages, loadNewerMessages]
  );

  return (
    <Box
      ref={listRef}
      className={`message-list-scroller ${className}`}
      style={{
        position: 'relative',
        height: '100%',
        overflowY: 'auto',
        ...style,
      }}
      onScroll={handleScroll}
    >
      {/*  */}
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          padding: 1,
          minHeight: 40,
        }}
      >
        {loading && <CircularProgress size={20} />}
      </Box>

      {/*  */}
      {children}

      {/*  */}
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          padding: 1,
          minHeight: 40,
        }}
      >
        {loadingNewer && <CircularProgress size={20} />}
      </Box>

      {/*  */}
      <Fade in={showScrollToBottom}>
        <Box
          sx={{
            position: 'sticky',
            bottom: 16,
            left: '50%',
            transform: 'translateX(-50%)',
            zIndex: 1,
          }}
        >
          <Button
            variant="contained"
            color="primary"
            onClick={() => scrollToBottom('smooth')}
            startIcon={
              <Badge
                badgeContent={newMessageCount}
                color="error"
                invisible={newMessageCount === 0}
              >
                <Box width={24} height={24} />
              </Badge>
            }
          >
            {t('scrollToBottom')}
          </Button>
        </Box>
      </Fade>
    </Box>
  );
});

MessageListScroller.displayName = 'MessageListScroller';

export { MessageListScroller };
export type { MessageListScrollerRef, MessageListScrollerProps };
